import { Component, OnInit,Input } from '@angular/core';
import { GListService } from './../g-list.service';
import { HelpersService } from './../helpers.service';
import { Grocery, MoreInformation } from '../Grocery';
import { FormatService } from '../frormat.service';
declare var jquery:any;
declare var $ :any;

@Component({
  selector: 'app-card-buttons',
  templateUrl: './card-buttons.component.html',
  styleUrls: ['./card-buttons.component.css']
})

//[editItem]='Item' [Item]='Item' 
//[bought]='(Item.moreInformations[ Item.moreInformations.length-1])?.bought '
export class CardButtonsComponent implements OnInit {
  @Input() timeoutDay ;
  buttonClick:boolean;

  //R
  @Input() lastmoreInformations:MoreInformation={bought:false  ,no:1 ,typeOfNo :""};
  //@Input() timeoutDay=0;
  NeededClicked:boolean=false;
  @Input() Item:Grocery;
  @Input() bought:boolean;//Determin What button to show Needed or Bought
  TheRandomString: string=this.helper.randomString();
  removeIdConfirm:string="#"+this.TheRandomString;
  removeConfirmForId:string=this.TheRandomString;
  
  //R
  constructor(private GListService:GListService,
    private formatService:FormatService,
    private helper:HelpersService) { }

  ngOnInit() {
  }



  edit(g:Grocery):void{  //(click) button Edit Item
    if (g.name =='')return//if the data is empty
    this.Edit(g)
  }
  Edit(g:Grocery){
    g.timeout =  this.timeoutDay*3600*24 +(Date.now()/1000)
    var lastmoreInformation=g.moreInformations[g.moreInformations.length-1]
    var grocery=this.formatService.ToSimpleedit(g)
   // console.log(grocery);
    this.GListService.UpdateStatus(grocery,"edit")
    //this.GListService.editG(g)
    this.buttonClick=false; 
  }

  //R

  remove(Item:Grocery):void{
    
    var grocery =this.formatService.Toremove(Item);
    //this.GListService.UpdateStatus(grocery,"remove");
    this.GListService.editG(grocery);

    //Close Dialog
    $(this.removeIdConfirm).modal('hide');
}

DELETE(Item:Grocery){
        //sendDelete
        var grocery =Item;
        this.GListService.UpdateStatus(grocery,"delete");
        //Close Dialog
        $(this.removeIdConfirm).modal('hide');
}

Bought(Item:Grocery){
  //Send Bought
  var grocery= this.formatService.Tobought(Item)
  console.log(grocery);
  this.GListService.UpdateStatus(grocery,"bought");
}

//Needed Logic
Needed(g:Grocery){   //(click) Needed button
  this.NeededClicked=!this.NeededClicked
  this.SendNeeded(g)
}
SendNeeded(g:Grocery){
  g.timeout =  this.timeoutDay*3600*24     
  var grocery= this.formatService.Toneed(g,g.basic,g.timeout,this.lastmoreInformations);
  this.GListService.UpdateStatus(grocery,"needed");
}
  //R


  MoreButton:boolean=false;
}//class





